.footer-container {
  width: 100%;
  background-color: #282c34;
  padding: 20px 0;
  position: relative;
  bottom: 0;
  color: white;
  text-align: center;
  text-color:white; 
}

.footer-space {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.footer-title {
  margin: 0;
  color: white !important;
}

.footer-link {
  color: white;
  margin: 0 10px;
  text-decoration: none;  
}

.footer-link:hover {
  text-decoration: underline;
}
